@font-face {
  font-family: 'icon';
  src:
    url('fonts/icon.ttf?68dixr') format('truetype'),
    url('fonts/icon.woff?68dixr') format('woff'),
    url('fonts/icon.svg?68dixr#icon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

